import React from 'react';
import { useState, useEffect } from 'react';
import './Search.css';
import { Link } from 'react-router-dom';

function Search() {
  // Dclaration de trois tats locaux pour grer les donnes de recherche, le terme de recherche et l'affichage des rsultats
  const [datas, setDatas] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [showResults, setShowResults] = useState(false);

  // Utilisation de useEffect pour effectuer une action aprs que le composant a t mont
  useEffect(() => {
    // Envoie d'une requte  l'API '/api/products' pour obtenir des donnes produits
    fetch('/api/products')
      .then((response) => response.json())
      .then((json) => setDatas(json));
  }, []); // Le tableau vide signifie que cela ne s'excute qu'une seule fois aprs le montage du composant
  // Gestionnaire de changement de terme de recherche
  const handleSearchTerm = (e) => {
    let value = e.target.value;
    // Met  jour l'tat 'searchTerm' avec la nouvelle valeur
    setSearchTerm(value);
    // Affiche les rsultats
    setShowResults(true);
  };
  // Gestionnaire de clic sur un rsultat
  const handleResultClick = () => {
    // Cache les rsultats lorsque l'utilisateur clique sur un rsultat
    setShowResults(false);
  };
  // Rendu du composant
  return (
    <>
      <div className="searchBar">
        {/* Input de recherche */}
        <input
          type="text"
          name="searchBar"
          id="searchBar"
          placeholder="Rechercher"
          onChange={handleSearchTerm} // Appelle le gestionnaire de changement de terme de recherche
        />
        {showResults && ( // Affiche les rsultats si 'showResults' est vrai
          <div className="search_results">
            {/* Filtrage et mappage des donnes pour afficher les rsultats */}
            {datas
              .filter((val) => {
                return val.name.includes(searchTerm);
              })
              .map((val) => {
                return (
                  // Cre un lien vers un produit spcifique
                  <Link
                    style={{ textDecoration: 'none' }}
                    to={`/product/${val.slug}`}
                    key={val.id}
                    onClick={handleResultClick} // Ajoute un gestionnaire de clic
                  >
                    {/* Affiche le nom du produit */}
                    <div className="search__result">{val.name}</div>
                  </Link>
                );
              })}
          </div>
        )}
      </div>
    </>
  );
}

// Exporte le composant Search pour pouvoir l'utiliser ailleurs
export default Search;
