import React, { useContext, useEffect, useState } from 'react';
import { Helmet } from 'react-helmet-async';
import { useNavigate } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import CheckoutSteps from '../components/CheckoutSteps';
import { Store } from '../Store';

export default function PaymentMethodScreen() {
  // Dfinit la fonction de composant PaymentMethodScreen
  const navigate = useNavigate(); // Rcupre la fonction de navigation

  const { state, dispatch: ctxDispatch } = useContext(Store); // Utilise le contexte global
  const {
    cart: { shippingAddress, paymentMethod }, // Destructuration de l'tat global
  } = state;

  const [paymentMethodName, setPaymentMethod] = useState(
    paymentMethod || 'PayPal'
  ); // Utilise l'tat local pour la mthode de paiement slectionne

  useEffect(() => {
    if (!shippingAddress.address) {
      // Si l'adresse de livraison n'est pas dfinie
      navigate('/shipping'); // Redirige vers l'cran de slection d'adresse de livraison
    }
  }, [shippingAddress, navigate]);

  const submitHandler = (e) => {
    // Gre la soumission du formulaire
    e.preventDefault(); // Empche le comportement de soumission par dfaut

    ctxDispatch({ type: 'SAVE_PAYMENT_METHOD', payload: paymentMethodName }); // Met  jour l'tat global avec la mthode de paiement
    localStorage.setItem('paymentMethod', paymentMethodName); // Stocke la mthode de paiement dans le stockage local
    navigate('/placeorder'); // Redirige vers l'cran de confirmation de commande
  };

  return (
    <div>
      <CheckoutSteps step1 step2 step3></CheckoutSteps>{' '}
      {/* Affiche les tapes de la commande */}
      <div className="container small-container">
        <Helmet>
          <title>Payment Method</title>
        </Helmet>
        <h1 className="my-3">Payment Method</h1>
        <Form onSubmit={submitHandler}>
          {' '}
          {/* Formulaire de slection de la mthode de paiement */}
          <div className="mb-3">
            <Form.Check
              type="radio"
              id="PayPal"
              label="PayPal"
              value="PayPal"
              checked={paymentMethodName === 'PayPal'}
              onChange={(e) => setPaymentMethod(e.target.value)}
            />{' '}
            {/* Option de paiement PayPal */}
          </div>
          <div className="mb-3">
            <Form.Check
              type="radio"
              id="Stripe"
              label="Stripe"
              value="Stripe"
              checked={paymentMethodName === 'Stripe'}
              onChange={(e) => setPaymentMethod(e.target.value)}
            />{' '}
            {/* Option de paiement Stripe */}
          </div>
          <div className="mb-3">
            <Button type="submit">Continue</Button> {/* Bouton de soumission */}
          </div>
        </Form>
      </div>
    </div>
  );
}
