import Axios from 'axios';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { Helmet } from 'react-helmet-async';
import { useContext, useEffect, useState } from 'react';
import { Store } from '../Store';
import { toast } from 'react-toastify';
import { getError } from '../utils';

// Dfinition de la fonction SignupScreen comme composant par dfaut
export default function SignupScreen() {
  // Utilisation du hook de navigation pour naviguer entre les pages
  const navigate = useNavigate();
  // Utilisation du hook de location pour accder aux paramtres de l'URL
  const { search } = useLocation();
  // Rcupration du paramtre 'redirect' de l'URL, s'il existe
  const redirectInUrl = new URLSearchParams(search).get('redirect');
  // Si 'redirectInUrl' est dfini, utilisez sa valeur, sinon utilisez '/'
  const redirect = redirectInUrl ? redirectInUrl : '/';

  // Dclaration de variables d'tat pour les champs du formulaire
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  // Utilisation du contexte pour accder aux donnes globales de l'application
  const { state, dispatch: ctxDispatch } = useContext(Store);
  // Extraction des informations sur l'utilisateur  partir de l'tat global
  const { userInfo } = state;

  // Fonction de gestion de la soumission du formulaire
  const submitHandler = async (e) => {
    e.preventDefault();
    // Vrification si les mots de passe correspondent
    if (password !== confirmPassword) {
      // Affichage d'une erreur en cas de non-correspondance des mots de passe
      toast.error('Passwords do not match');
      return;
    }
    try {
      // Envoi d'une requte POST pour l'inscription de l'utilisateur
      const { data } = await Axios.post('/api/users/signup', {
        name,
        email,
        password,
      });
      // Mise  jour de l'tat global avec les donnes de l'utilisateur connect
      ctxDispatch({ type: 'USER_SIGNIN', payload: data });
      // Stockage des informations de l'utilisateur dans le stockage local
      localStorage.setItem('userInfo', JSON.stringify(data));
      // Redirection de l'utilisateur vers la page spcifie dans 'redirect' ou '/'
      navigate(redirect || '/');
    } catch (err) {
      // Gestion des erreurs en cas d'chec de l'inscription
      toast.error(getError(err));
    }
  };

  // Effet qui redirige l'utilisateur s'il est dj connect
  useEffect(() => {
    if (userInfo) {
      navigate(redirect);
    }
  }, [navigate, redirect, userInfo]);

  // Rendu du composant
  return (
    <Container className="small-container">
      <Helmet>
        <title>S'inscrire</title>
      </Helmet>
      <h1 className="my-3">S'inscrire</h1>
      <Form onSubmit={submitHandler}>
        {/* Champ de formulaire pour le nom */}
        <Form.Group className="mb-3" controlId="name">
          <Form.Label>Nom</Form.Label>
          <Form.Control onChange={(e) => setName(e.target.value)} required />
        </Form.Group>

        {/* Champ de formulaire pour l'email */}
        <Form.Group className="mb-3" controlId="email">
          <Form.Label>Email</Form.Label>
          <Form.Control
            type="email"
            required
            onChange={(e) => setEmail(e.target.value)}
          />
        </Form.Group>

        {/* Champ de formulaire pour le mot de passe */}
        <Form.Group className="mb-3" controlId="password">
          <Form.Label>Mot de passe</Form.Label>
          <Form.Control
            type="password"
            required
            onChange={(e) => setPassword(e.target.value)}
          />

          {/* Champ de formulaire pour la confirmation du mot de passe */}
          <Form.Group className="mb-3" controlId="confirmPassword">
            <Form.Label>Confirmez Mot de passe</Form.Label>
            <Form.Control
              type="password"
              onChange={(e) => setConfirmPassword(e.target.value)}
              required
            />
          </Form.Group>
        </Form.Group>

        {/* Bouton de soumission du formulaire */}
        <div className="mb-3">
          <Button type="submit" className="mb-3">
            S'inscrire
          </Button>
        </div>

        {/* Lien pour se connecter si l'utilisateur a dj un compte */}
        <div className="mb-3">
          Vous avez dj un compte ?{' '}
          <Link to={`/signin?redirect=${redirect}`}>Se connecter</Link>
        </div>
      </Form>
    </Container>
  );
}
