import React, { useEffect, useReducer } from 'react';
import axios from 'axios';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Product from '../components/Product';
import { Helmet } from 'react-helmet-async';
import LoadingBox from '../components/LoadingBox';
import MessageBox from '../components/MessageBox';

// Dfinition d'un rducteur (reducer) pour grer les actions sur l'tat global
const reducer = (state, action) => {
  switch (action.type) {
    case 'FETCH_REQUEST':
      // En cas de demande de chargement, met  jour l'tat pour indiquer le chargement en cours
      return { ...state, loading: true };
    case 'FETCH_SUCCESS':
      // En cas de succs de la requte, met  jour l'tat avec les donnes reues et indique que le chargement est termin
      return { ...state, products: action.payload, loading: false };
    case 'FETCH_FAIL':
      // En cas d'chec de la requte, met  jour l'tat avec un message d'erreur et indique que le chargement est termin
      return { ...state, loading: false, error: action.payload };
    default:
      // Par dfaut, renvoie simplement l'tat inchang
      return state;
  }
};

// Dfinition d'une fonction composant Quicksilver
function Quicksilver() {
  // Utilisation du hook useReducer pour grer l'tat global avec le rducteur prcdemment dfini
  const [{ loading, error, products }, dispatch] = useReducer(reducer, {
    products: [], // Tableau vide pour stocker les produits
    loading: true, // Indicateur de chargement initialis  vrai
    error: '', // Message d'erreur initialis comme une chane vide
  });

  // Utilisation du hook useEffect pour effectuer des oprations asynchrones lors du chargement du composant
  useEffect(() => {
    const fetchData = async () => {
      // Dclenche une action FETCH_REQUEST pour indiquer le dbut du chargement
      dispatch({ type: 'FETCH_REQUEST' });
      try {
        // Effectue une requte GET pour obtenir des produits depuis l'API
        const result = await axios.get('/api/products');
        // En cas de succs, dclenche une action FETCH_SUCCESS avec les donnes reues
        dispatch({ type: 'FETCH_SUCCESS', payload: result.data });
      } catch (err) {
        // En cas d'chec, dclenche une action FETCH_FAIL avec le message d'erreur
        dispatch({ type: 'FETCH_FAIL', payload: err.message });
      }
    };
    // Appelle la fonction fetchData lors du montage initial du composant (dpendance vide [])
    fetchData();
  }, []);

  // Rendu du composant Quicksilver
  return (
    <div>
      <Helmet>
        <title>Quicksilver</title>
      </Helmet>
      <h1>Produit Quicksilver</h1>
      <div className="products">
        {loading ? (
          // Affiche un composant de chargement si le chargement est en cours
          <LoadingBox />
        ) : error ? (
          // Affiche un composant de bote d'erreur en cas d'erreur
          <MessageBox variant="danger">{error}</MessageBox>
        ) : (
          // Affiche la liste des produits si le chargement est termin sans erreur
          <Row>
            {products
              .filter((product) => product.brand === 'Quicksilver')
              .map((product) => (
                <Col key={product.slug} sm={6} md={4} lg={3} className="mb-3">
                  <Product product={product}></Product>
                </Col>
              ))}
          </Row>
        )}
      </div>
    </div>
  );
}

// Exporte la fonction Quicksilver comme composant par dfaut
export default Quicksilver;
