import React, { useContext, useReducer, useState } from 'react';
import { Store } from '../Store';
import { Helmet } from 'react-helmet-async';
import { Button, Form } from 'react-bootstrap';
import { toast } from 'react-toastify';
import { getError } from '../utils';
import axios from 'axios';
import { logoutAction } from '../components/authReducer'; // Assurez-vous d'importer correctement l'action

// Dfinition d'un rducteur (reducer) pour grer les actions lies  la mise  jour du profil
const reducer = (state, action) => {
  switch (action.type) {
    case 'UPDATE_REQUEST':
      // En cas de demande de mise  jour, met  jour l'tat pour indiquer le chargement en cours
      return { ...state, loadingUpdate: true };
    case 'UPDATE_SUCCESS':
      // En cas de succs de la mise  jour, met  jour l'tat pour indiquer que la mise  jour est termine avec succs
      return { ...state, loadingUpdate: false };
    case 'UPDATE_FAIL':
      // En cas d'chec de la mise  jour, met  jour l'tat pour indiquer que la mise  jour a chou
      return { ...state, loadingUpdate: false };
    default:
      // Par dfaut, renvoie simplement l'tat inchang
      return state;
  }
};

// Dfinition de la fonction ProfileScreen
export default function ProfileScreen() {
  // Utilisation du contexte pour accder aux donnes globales de l'application
  const { state, dispatch: ctxDispatch } = useContext(Store);
  const { userInfo } = state;

  // Initialisation des tats locaux avec les informations du profil de l'utilisateur
  const [name, setName] = useState(userInfo.name);
  const [email, setEmail] = useState(userInfo.email);
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  // Utilisation du hook useReducer pour grer l'tat local li  la mise  jour
  const [{ loadingUpdate }, dispatch] = useReducer(reducer, {
    loadingUpdate: false,
  });

  // Fonction de gestion de la soumission du formulaire de mise  jour du profil
  const submitHandler = async (e) => {
    e.preventDefault();
    try {
      // Effectue une requte PUT pour mettre  jour le profil de l'utilisateur
      const { data } = await axios.put(
        '/api/users/profile',
        {
          name,
          email,
          password,
        },
        {
          headers: { Authorization: `Bearer ${userInfo.token}` },
        }
      );
      // Dclenche une action de succs de la mise  jour
      dispatch({
        type: 'UPDATE_SUCCESS',
      });
      // Met  jour les donnes de l'utilisateur dans l'tat global
      ctxDispatch({ type: 'USER_SIGNIN', payload: data });
      // Stocke les nouvelles informations de l'utilisateur dans le stockage local
      localStorage.setItem('userInfo', JSON.stringify(data));
      // Affiche un message de succs
      toast.success('User updated successfully');
    } catch (err) {
      // En cas d'erreur, dclenche une action d'chec de la mise  jour
      dispatch({
        type: 'FETCH_FAIL',
      });
      // Affiche un message d'erreur
      toast.error(getError(err));
    }
  };

  // ...

  const deleteAccountHandler = async () => {
    if (window.confirm('Are you sure you want to delete your account?')) {
      try {
        // Effectuez la requte DELETE pour supprimer le compte utilisateur
        await axios.delete('/api/users/profile', {
          headers: {
            Authorization: `Bearer ${userInfo.token}`,
          },
        });

        // Aprs la suppression russie, dconnectez l'utilisateur en appelant l'action de dconnexion
        ctxDispatch(logoutAction());

        // Supprimez galement les informations de l'utilisateur dans le stockage local si ncessaire
        localStorage.removeItem('userInfo');

        window.location.reload();
        // Affichez un message de succs
        toast.success('Account deleted successfully');
      } catch (err) {
        // En cas d'erreur, affichez un message d'erreur
        toast.error(getError(err));
      }
    }
  };

  // Rendu du composant ProfileScreen
  return (
    <div className="container small-container">
      <Helmet>
        <title>User Profile</title>
      </Helmet>
      <h1 className="my-3">User Profile</h1>
      <form onSubmit={submitHandler}>
        {/* Champ de formulaire pour le nom */}
        <Form.Group className="mb-3" controlId="name">
          <Form.Label>Name</Form.Label>
          <Form.Control
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </Form.Group>

        {/* Champ de formulaire pour l'e-mail */}
        <Form.Group className="mb-3" controlId="name">
          <Form.Label>Email</Form.Label>
          <Form.Control
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </Form.Group>

        {/* Champ de formulaire pour le mot de passe */}
        <Form.Group className="mb-3" controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            onChange={(e) => setPassword(e.target.value)}
          />
        </Form.Group>

        {/* Champ de formulaire pour la confirmation du mot de passe */}
        <Form.Group className="mb-3" controlId="password">
          <Form.Label>Confirm Password</Form.Label>
          <Form.Control
            type="password"
            onChange={(e) => setConfirmPassword(e.target.value)}
          />
        </Form.Group>

        {/* Bouton de soumission du formulaire */}
        <div className="mb-3">
          <Button type="submit">Update</Button>
        </div>
      </form>
      <div className="mb-3">
        <Button variant="danger" onClick={deleteAccountHandler}>
          Delete Account
        </Button>
      </div>
    </div>
  );
}
